
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
.laneWrapper{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  justify-items: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.lane{
  width: 240px;
}
.isError{
  background-color: #ffaaaa;
}
.row{
  display:-webkit-box;
  display:-ms-flexbox;
  display:flex;
  width: 100%;
}
.column{
  width: 50%;
}
.elem{
  width: 50%;
}
























































